.stepper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--light-grey);
  padding: 12px 16px;
  width: 122px;
  height: 46px;
}
.stepper__btn {
  width: 25px;
  height: 25px;
  border: 2px solid transparent;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  text-align: center;
  color: var(--border-dark);
  transition: border 0.3s ease-in-out, color 0.3s ease-in-out;
  outline: none;
}
.stepper__btn:hover {
  color: var(--dark-primary);
}
.stepper__btn:focus {
  border: 2px solid var(--dark-primary);
}
.stepper__btn:active {
  color: var(--dark-primary);
}
.stepper__count {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  color: var(--dark-primary);
}